	module.exports = {"data":{"wordpressWpLessons":{"id":"19d1c821-8903-5c08-8047-1452bf6b6412","wordpress_id":1132,"title":"Graphic Design With Gimp Mini Project","slug":"graphic-design-with-gimp-mini-project","content":"<h2>Project Overview</h2>\n<p>There are two parts to this activity: the project and the Maker&#8217;s Reflection.<br />\nPlease read the prompt and guidelines for the project and begin working on it.</p>\n<p>Before beginning your project, you can plan it out and sketch it in your Maker Notebook. Then, create your project. Once you are done, you will follow the instructions at the bottom of this post to create the Maker&#8217;s Reflection document. Once you&#8217;re done, show an instructor your entry.</p>\n<p>Please focus on making sure these projects are solid and well documented. Completing and documenting your projects is required for DHF Membership.</p>\n<h2>Project Prompt</h2>\n<p><b><strong><span style=\"color: #cf0000;\">IMPORTANT: Please read this ENTIRE section before starting!</span></strong></b></p>\n<p>Please use the following prompt to guide your project:</p>\n<p><b><em>Prompt: Create Pixel Sprites</em></b><br />\nThe prompt for this mini project is connected to the <a href=\"https://www.classcraft.com/\" target=\"_blank\" rel=\"noopener\">Classcraft</a> quest, &#8220;A Grand Design.&#8221; Using GIMP, you will create four enemy sprites for four different environments &#8211; a cave, a swamp, a tundra, and a forest. Along with a demonstration of creativity, we expect your design to reflect the following skills:</p>\n<ul>\n<li>Pixel Art Toolbar Presets: Eraser and Pencil</li>\n<li>Resized canvas with transparent background</li>\n<li>Properly sized canvas for Pixel Art</li>\n<li>Multiple colors: Shading and Dithering are bonuses!</li>\n<li>Layering: If applicable, demonstrate layering knowledge</li>\n</ul>\n<p><b><em>Prompt (Bonus!): Logo Art</em></b></p>\n<p>Using GIMP, design a logo or splash (intro) screen for a future Scratch game.<br />\nYour project should meet the following criteria:</p>\n<ul>\n<li>Pixel Art Toolbar Presets: Eraser and Pencil</li>\n<li>Resized canvas with a transparent background</li>\n<li>Properly sized canvas for Pixel Art</li>\n<li>Multiple colors: Shading and Dithering are bonuses!</li>\n<li>Layering: Demonstrate layering knowledge</li>\n</ul>\n<p><b><em>REMEMBER:</em></b></p>\n<ul>\n<li>Save your work!\n<ul>\n<li>The first time you save your project, use the <span style=\"color: #0091c9;\">File -&gt; Save As</span> option and give your document a name and save it to the &#8220;Documents&#8221; folder instead of the default location. While you are working, save your work every once in a while under <span style=\"color: #0091c9;\">File -&gt; Save</span>. When you are all done, upload the work to Google Drive.</li>\n</ul>\n</li>\n<li>Take screenshots!\n<ul>\n<li>You can create screenshots to document your project as you&#8217;re working as well as upload the final product. This is a good skill to practice as you&#8217;ll want to get in the habit of documenting a project as you&#8217;re working on it. Also, feel free to take pictures of any sketches that you&#8217;ve done in your Maker Notebook!</li>\n</ul>\n</li>\n</ul>\n<h2>Maker&#8217;s Reflection</h2>\n<p>First, you&#8217;ll need to create a Google Document for your Maker&#8217;s Reflection. Here are the steps to do this:</p>\n<ol>\n<li>Log in with your DHF email address if you aren&#8217;t already.</li>\n<li>Navigate to <a href=\"https://drive.google.com\">Google Drive</a>. Since you&#8217;re already logged in with your email, you&#8217;ll be taken directly to your personal drive.</li>\n<li>Select <strong>My Drive</strong> from the left sidebar and open up the course drive folder.<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/mkfdn-doc-gdrive_1-600px1.jpg\" alt=\"Maker Foundations: Google Drive - Course Drive\" /></li>\n<li>Locate (and click to open) your cohort folder and find (and open) the folder with your name on it. In this example, Steph&#8217;s sample folder is selected:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/mkfdn-doc-gdrive_3-600px3.jpg\" alt=\"Maker Foundations: Google Drive - Name Folder\" /></li>\n<li>Open the <em>Graphic Design</em> folder:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/mf-graphic_design-makers_reflection_1-600px3.jpg\" alt=\"Maker Foundations: Makers Reflection 1\" /></li>\n<li>Once in the folder, click the <strong>New</strong> button in the top left corner:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/mf-graphic_design-makers_reflection_2-600px1.jpg\" alt=\"Maker Foundations: Makers Reflection 2\" /></li>\n<li>Click the &#8220;Google Docs&#8221; option from the drop-down menu to create a document:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/mf-graphic_design-makers_reflection_3-600px2.jpg\" alt=\"Maker Foundations: Makers Reflection 3\" /></li>\n<li>Name the document like the example: &#8220;Maker&#8217;s Reflection &#8211; Graphic Design &#8211; FirstInitialLastName &#8211; 16-T3&#8221;</li>\n</ol>\n<p>Answer these 3 questions in your reflection.</p>\n<ul>\n<li>What was the project prompt?</li>\n<li>What is your project?</li>\n<li>Why did you make this project?</li>\n</ul>\n<p>Then, choose at least 2 from the list below to respond to.</p>\n<ul>\n<li>How did you plan or prototype your project before starting?</li>\n<li>How did you make it? What was your process? What steps did you follow?</li>\n<li>What problems or challenges did you face?</li>\n<li>How did you overcome any challenges and solve problems that you met?</li>\n<li>What would you do differently next time?</li>\n<li>What would you tell someone else who was going to make this project?</li>\n<li>What did you like best about your project?</li>\n<li>How would you make it better?</li>\n</ul>\n"}},"pathContext":{}}

